#Canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  transition: 0.5s all;
  animation: fade-in 12s;
  // animation-delay: 2s;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
  
}


